import { __decorate } from "tslib";
import { Component, Vue } from 'vue-property-decorator';
let payAdmin = class payAdmin extends Vue {
    constructor() {
        super(...arguments);
        this.openid = '';
    }
    activated() {
        this.pageInit();
    }
    pageInit() {
        this.openid = this.$route.query.openid;
    }
    showClick() {
        if (this.openid) {
            this.$router.replace({ path: '/orderList', query: { openid: this.openid } });
        }
    }
};
payAdmin = __decorate([
    Component({
        components: {},
        filters: {}
    })
], payAdmin);
export default payAdmin;
